.accordion__item {
  width: 100%;
  letter-spacing: .02em;
  color: var(--text-color);
  border-bottom: 1px solid #929292;
  flex-direction: column;
  align-items: start;
  margin-top: 30px;
  margin-bottom: 20px;
  padding-bottom: 12px;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.71;
  transition: transform .3s;
  display: flex;
}

.accordion__title {
  cursor: pointer;
  width: 30%;
  margin: 0;
  padding: 1rem;
}

.accordion__content {
  width: 100%;
  padding: 1rem;
  transition: all 3s;
  display: none;
}

.accordion__item.active .accordion__content {
  flex-wrap: wrap;
  justify-content: space-between;
  transition: transform .3s;
  display: flex;
  transform: translateY(10px);
}

.accordion__icon {
  width: 16px;
  height: 16px;
  background-image: url("arrow-down-icon.16d3f2d1.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 8px;
  transition: transform .3s;
  display: inline-block;
  position: relative;
  top: 4px;
}

.accordion__item.active .accordion__icon {
  background-image: url("arrow-down-icon.16d3f2d1.svg");
  transform: rotate(180deg)translateY(10px);
}

.accordion__item.active {
  transform: translateY(10px);
}

@keyframes beat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(1.25);
  }

  15% {
    animation-timing-function: ease-out;
    transform: scale(1.05);
  }

  25% {
    animation-timing-function: ease-in;
    transform: scale(1.15);
  }

  35% {
    animation-timing-function: ease-out;
    transform: scale(1.05);
  }
}

